import React from "react";

function BestRowOfDays(props) {

    const regDay = /(\d{1,2})\.[\.0-9]+/

    let sessionsHistory = props.sessionsHistory.map(e => {return e.date})

    let properDaysFormat = sessionsHistory
        .map((e) => {
            let a = e.match(regDay)
            let rawNum = a[1]
            let num = Number(rawNum)
            return num
        })


    // Массив который показывает дни подряд в медитации
    let daysInARowArray = properDaysFormat
        .map(function (firstDay, index, array) {
            let push = [[1]]
            let skipNumber = 0
            index += skipNumber
            for (let m = 1, i = array[index]; !push[push.length - 1].includes(firstDay) && firstDay === array[index + m] - 1;) {
                let secondDay = array[index + m]
                push.push(firstDay)
                push.push(secondDay)
                for (let y = 1, nextDays = array[index + y]; nextDays === array[index + y + 1] - 1; y++, nextDays++) {
                    push.push(array[index + y + 1])
                    skipNumber = y
                }
                return push
            }
        }
        )
        .filter(function (value, index, self) {
            return self.indexOf(value) === index;
        })
        .filter(function (firstDay) {
            return firstDay !== undefined
        })

    const arrayLengths = []

    for (let i = 0; i < daysInARowArray.length; i++) {
        arrayLengths.push(daysInARowArray[i].length)
    }

    let theGreatestResult = 1

    for (let i = 0; i < arrayLengths.length; i++) {
        if (arrayLengths[i] > theGreatestResult) {
            theGreatestResult = arrayLengths[i]
        }
    }

    return sessionsHistory.length !== 0 ? <div className="backing fields">
        <h3>Ваш рекорд медитации:</h3>
        <h2> {theGreatestResult} дней </h2>
    </div> 
    :
    <div></div>
}

export default BestRowOfDays