import React from "react"
import OverallMeditationTime from "./AllTimeRecording/OverallMeditationTime"
import BestRowOfDays from "./BestRowOfDays/BestRowOfDays"
import HistoryElement from "./HistoryElement/HistoryElement"
import './HistoryPage.css'
import CalendarContainer from "./calendar/CalendarContainer";

function HistoryPage( {sessionsHistory, overallMeditationTime} ) {
        let currentMonth = new Date().getMonth() + 1;
        let currentYear = new Date().getFullYear();

        return (
        <div>
            <div className="infoDate">
            <div className='topPageElement'>
            <HistoryElement sessionsHistory = {sessionsHistory} />
            </div>

                <div className='topPageElement'>
            <BestRowOfDays sessionsHistory = {sessionsHistory} />
            </div>

                <div className='topPageElement'>
            <OverallMeditationTime overallMeditationTime = {overallMeditationTime} />
            </div>
            </div>

            <div>
            <CalendarContainer currentMonth={currentMonth} currentYear={currentYear} />
            </div>
        </div>)
}

export default HistoryPage