import React from "react"
import './Button.css'

let Button = (props) => {

        let buttonHandler = () => {
            let timesecs = props.time
            let time = timesecs * 60
            props.setNewTimeLeft(time)
            props.startTimer(time)
            }
        return <button disabled={props.isDisabled} className='button timerDigit' onClick={buttonHandler} > {props.time}</button>

}


export default Button