import React from "react";
import StartButton from './../../../assets/timer/play-button.png'
import PauseButton from './../../../assets/timer/pause-button.png'

let StartPauseButton = function (props) {

    let clickHandler = () => {
        let timesecs = props.enteredTime;
        let time = timesecs * 60
        props.startPauseResume(time)
        props.setEnteredTime('')
    }

    return <div> <img className="button" src={props.timerIsOn
        ? PauseButton
        : StartButton
    }
    onClick={clickHandler}></img>

    </div>
}

export default StartPauseButton