import './AudioPlayer.css'
import './../../backing.css'
import React, { useEffect, useRef, useState } from 'react'
import buttonPlay from './../../../assets/player/play-button.png'
import buttonNext from './../../../assets/player/next-button.png'
import buttonPrev from './../../../assets/player/back-button.png'
import buttonPause from './../../../assets/player/pause-button.png'
import buttonStop from './../../../assets/player/stop-button.png'
import buttonReload from './../../../assets/player/reload.png'

let AudioPlayer = (props) => {
    useEffect(() => {
        document.title = 'Медитация'
    })

    useEffect(() => {
        if (props.timerIsOn === true && props.autoplayIsOn === true) {
            player.current.play();
        } else {
            player.current.pause();
        }
    }, [props.timerIsOn]);

    useEffect(() => {
        player.current.addEventListener('timeupdate', updateProgress)
    });
    useEffect(() => {
        player.current.addEventListener('loadeddata', updateProgress)
    });

    useEffect(() => {
        player.current.addEventListener('loadeddata', getFullDuration)
    });

    useEffect(() => {
        progressBarContainer.current.addEventListener('click', setProgress)
    });

    let [currentTime, setCurrentTime] = useState('0:00')
    let [fullDuration, setFullDuration] = useState('0:00')
    let [isPlaying, setIsPlaying] = useState(false)

    let player = useRef()
    let button = useRef()
    let progressBar = useRef()
    let progressBarContainer = useRef()

    let playMusic = () => {
        setIsPlaying(true)
        player.current.play()
    }

    let pauseMusic = () => {
        setIsPlaying(false)
        player.current.pause()
    }

    let stopMusic = () => {
        setIsPlaying(false)
        player.current.pause()
        player.current.currentTime = 0
    }

    let restartMusic = () => {
        setIsPlaying(true)
        player.current.pause()
        player.current.currentTime = 0
        player.current.play()
    }

    function playPause() {
        if (!isPlaying) {
            playMusic()
            return
        } 
        pauseMusic()   
    }

    const propersSongNames = props.rawSongNames.map((el) => {
        let final = el.match(/[\s\S]+media\/([^\.]+)\.[0-9a-zA-Z\ ]+\.mp3/)
        let s = final[1]
        return s
    })

    let prevSong = function () {
        if (props.songIndex > 0) {
            let a = props.songIndex
            props.changeSongIndex(a - 1)
            props.chooseSong(props.rawSongNames[a - 1])
            stopMusic()
            return
        }
        return false
    }

    let nextSong = function () {
        if (props.songIndex < 2) {
            let a = props.songIndex
            props.changeSongIndex(a + 1)
            props.chooseSong(props.rawSongNames[a + 1])
            stopMusic()
            return
        }
        return false
    }

    let autoplay = () => {
        if (props.autoplayIsOn) {
            props.turnAutoPlayOnOff(false)
        } else {
            props.turnAutoPlayOnOff(true)
        }
    }

    function redesingTime(secs) {
        var hr = Math.floor(secs / 3600);
        var min = Math.floor((secs - (hr * 3600)) / 60);
        var sec = Math.floor(secs - (hr * 3600) - (min * 60));
        if (sec < 10) {
            sec = "0" + sec;
        }
        return min + ':' + sec;
    }

    // получает полную длительность трека
    function getFullDuration(e) {
        let durationRaw = e.target.duration
        let duration = redesingTime(durationRaw);
        setFullDuration(duration)
    }

    // обновляет текущую позицию (время)
    function updateProgress(e) {
        const { duration, currentTime } = e.target
        const progressPercent = (currentTime / duration) * 100
        progressBar.current.style.width = `${progressPercent}%`
        let chosenMoment = redesingTime(player.current.currentTime.toString());
        setCurrentTime(chosenMoment)
    }

    // установить позицию на прогрессбаре
    function setProgress(e) {
        const width = this.clientWidth
        const clickX = e.offsetX
        const duration = player.current.duration;
        player.current.currentTime = (clickX / width) * duration
    }

    return (<div className='backing playerWrapper'>
        <div className='titleAndSwitcherWrapper'>
            <h4 className='title'>Плеер</h4>
        </div>
        <div>
            <h3 className='songName'>{propersSongNames[props.songIndex]}</h3>
        </div>
        <div className='buttons PrevNext'>
            <img src={buttonPrev} className='button Prev' onClick={prevSong}></img>
            <img src={buttonNext} className='button Next' onClick={nextSong}></img>
        </div>

        <audio ref={player} src={props.currentSong} preload loop></audio>


        <div className='timeAndProgress'>
            <div className='timeInfoWrapper'>
                <div className="time current">{currentTime}</div>
                <div className="time duration">{fullDuration}</div>
            </div>
            <div ref={progressBarContainer} className='progressContainer'>
                <div ref={progressBar} className='progress'></div>
            </div>
        </div>
        <div className='buttons PSR'>
            <img src={isPlaying ? buttonPause : buttonPlay} className='button Play' ref={button} onClick={playPause}></img>
            <img src={buttonStop} className='button Stop' onClick={stopMusic}></img>
            <img src={buttonReload} className='button Restart' onClick={restartMusic}></img>
        </div>

        <div className='autoplaySwitch'> 
        <h3 className={props.autoplayIsOn 
            ? 'switcherText on' 
            : 'switcherText off'} 
            onClick={autoplay}>
                Автопроигрывание
                </h3>

        </div>

    </div>)
}


export default AudioPlayer