import React from "react";

function OverallMeditationTime ({overallMeditationTime}) {
     return overallMeditationTime ? 
        <div className="backing fields">
        <h3>Проведено в медитации:</h3>
        <h2> {overallMeditationTime} секунд </h2>
        </div> 
        : 
        <div></div>
        }
        
export default OverallMeditationTime