import './Calendar.css'
import './../../backing.css'

let Calendar = (props) => {
    let currentMonth = new Date().getMonth() + 1;
    let currentYear = new Date().getFullYear();

    let createCalendar = (selectedMonth, currentYear) => {

        let isMore = (selectedMonth) => {
            if (selectedMonth > 12) {
                return true
            } else {
                return false
            }
        }

        let monthNames = ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь']
        let dayOfWeekArray = ['ПН', 'ВТ', 'СР', 'ЧТ', 'ПТ', 'СБ', 'ВС']

        // возвращается массив дней недели
        let dayOfWeekElements = dayOfWeekArray.map((e) => {
            return <div className='dayOfWeek'>{e}</div>
        })

        const getNumberOfDays = (y, m) => new Date(y, m, 0).getDate();
        let numberOfDays = getNumberOfDays(currentYear, isMore(selectedMonth) ? 1 : selectedMonth)
        let CalendarElements = []

        for (let i = 1; i <= numberOfDays; i++) {
            CalendarElements.push({day: i, month: selectedMonth, year: currentYear})
        }

        // делалась ли медитация в определённый день.
        let filterObj = (e) => {
            return props.sessionsHistory.some(function (obj) {
                let year = e.year
                let month = e.month;
                let day = e.day
                let lateDate = day + '.' + month + '.' + year
                return (obj.date === lateDate)
            })
        }

        const getDayOfWeek = (selectedMonth) => {
            let date = new Date(currentYear, selectedMonth);
            let dayOfWeek = date.getDay();
            return dayOfWeek; 
        }

        let blankFirstDays = getDayOfWeek(isMore(selectedMonth) ? 0 : selectedMonth - 1)

        let monthName = monthNames[isMore(selectedMonth) ? 0 : selectedMonth - 1]

        for (let i = 1; i < blankFirstDays; i++) {
            CalendarElements.unshift('') // добавляет пустые клетки перед началом месяца
        }

        let dayElements = CalendarElements.map((e) => {
            return <div className={filterObj(e) ? 'el active' : 'el'}> {e.day}</div>
        })

        let oneMonth = <div>           
            <h2>{monthName}</h2>
            <div className='one_Month'>
                {dayOfWeekElements}
                {dayElements}
            </div>
        </div>
        return oneMonth
    }

    // проверяет не прошлый ли год
    let checkLastYear = (prevM) => {
        return prevM < 1
    }

    let previousMonth = currentMonth - 1
    let monthBeforePrevious = currentMonth - 2
    
    let prevPrevMonth = createCalendar(checkLastYear(monthBeforePrevious) ? 12 : monthBeforePrevious, checkLastYear(monthBeforePrevious) ? currentYear - 1 : currentYear)
    let prevMonth = createCalendar(checkLastYear(previousMonth) ? 12 : previousMonth, checkLastYear(previousMonth) ? currentYear - 1 : currentYear)
    let ongoingMonth = createCalendar(currentMonth, currentYear)

    return (
        <div className='calendar_wrap'>
            <div className='calendar_Header'>
                <h1>КАЛЕНДАЛЬ</h1>
            </div>

            <div className='calendar_container'>
                <div className='one_Month_container'>
                    {prevPrevMonth}
                </div>

                <div className='one_Month_container'>
                    {prevMonth}
                </div>

                <div className='one_Month_container'>
                    {ongoingMonth}
                </div>
            </div>
        </div>
    )
}

export default Calendar