import React, { useState } from "react";
import TimerContainer from "./timer/TimerContainer";
import AudioPlayerContainer from "./AudioPlayer/AudioPlayerContainer";
import './HomePage.css'

let HomePage = function () {
    let [pleerIsOn, setPleerIsOn] = useState(true)


    return <div className='homePage'>
        <div className="timerComponent">
        <TimerContainer />
        </div>

        <div className="timerElementsWrapper">
        <div className="button">
            <button onClick={() => {
                pleerIsOn
                    ? setPleerIsOn(false)
                    : setPleerIsOn(true)
            }
            }>
                {pleerIsOn
                    ? 'Закрыть плеер'
                    : 'Открыть плеер'
                }
            </button>
        </div>

        <div>
            {pleerIsOn
                ? <div> <AudioPlayerContainer setPleerIsOn={setPleerIsOn}  /> </div>
                : <div> Плеер закрыт </div>
            }
        </div>

        </div>

    </div>
}



export default HomePage